import React from "react";

import {
    Group,
    Panel,
    PanelHeader,
    ContentCard,
    Div,
    Card

} from '@vkontakte/vkui';


import { useAtomValue } from "@mntm/precoil";
import { desktopAtom } from "../../store/atoms";

import API from "../../store/api/api";


const Actions = ({ id }) => {
    const desktop = useAtomValue(desktopAtom)
    const [posts, setPosts] = React.useState([])
    const api = new API()

    React.useEffect(() => {
        api.get_events().then(r => {
            setPosts([])
            r.events.map((event_id) =>
                api.get_event(event_id["$oid"]).then(event => {
                    setPosts(posts => [...posts,
                        <div key={event_id["$oid"]}>
                            <ContentCard
                                style={{cursor: "default"}}
                                src={event.src}
                                header={event.header}
                                text={event.text}
                                maxHeight={event.max_height}
                            /> <Div/>
                        </div>
                    ])
                })
            )
        })
    }, [])

    return (
        <Panel id={id}>
            <PanelHeader/>
            <Group style={{paddingTop: desktop ? "16px" : "0"}}>
                {posts}
            </Group>
        </Panel>

    )
}

export default Actions;