import React from "react";

import {
    Group,
    Panel,
    PanelHeader,
    List,
    FormItem,
    File,
    IconButton,
    SimpleCell,
    Div,
    Separator,
    Header,
    FormLayout,
    Input,
    Textarea,
    FormLayoutGroup,
    ContentCard,
    Button

} from '@vkontakte/vkui';

import {
    Icon28CancelCircleFillRed,
    Icon28DocumentOutline,
    Icon24WriteOutline,
    Icon24Camera,
    Icon28DownloadCloudOutline
} from "@vkontakte/icons";

import { useAtomValue } from "@mntm/precoil";
import { desktopAtom } from "../../store/atoms";

import API from "../../store/api/api";


const onFileInputChange = (e) => {
    if (e.target.files.length > 0) {
        const url = window.URL.createObjectURL(e.target.files[0])
        return fetch(url)
            .then(response => response.blob())
            .then(blob => new Promise((resolve, reject) => {
                const reader = new FileReader()
                reader.onloadend = () => resolve(reader.result)
                reader.onerror = reject
                reader.readAsDataURL(blob)
            }))
    }
}

const AdminPanel = ({ id }) => {
    const desktop = useAtomValue(desktopAtom)
    const api = new API()
    const [events, setEvents] = React.useState([])
    const [docs, setDocs] = React.useState([])

    const [eventUniqueName, setEventUniqueName] = React.useState("")
    const [header, setHeader] = React.useState("")
    const [text, setText] = React.useState("")
    const [maxHeight, setMaxHeight] = React.useState("")
    const [image, setImage] = React.useState("")
    const [doc, setDoc] = React.useState("")
    const [docUniqueName, setDocUniqueName] = React.useState("")

    React.useEffect(() => {
        api.get_event_names().then(r => {
            r.map((event) => {
                setEvents(events => [...events,
                    <SimpleCell
                        key={event[Object.keys(event)[0]]["$oid"]}
                        before={<Icon28DocumentOutline/>}
                        after={
                            <IconButton onClick={() => {
                                api.delete(event[Object.keys(event)[0]]["$oid"]).then()
                                window.location.replace("https://dagvodres.ru/panel")
                            }}>
                                <Icon28CancelCircleFillRed/>
                            </IconButton>
                        }
                    >
                        {Object.keys(event)[0]}
                    </SimpleCell>
                ])
            })
        })

        api.get_document_names().then(r => {
            r.map((doc) => {
                setDocs(docs => [...docs,
                    <SimpleCell
                        key={doc[Object.keys(doc)[0]]["$oid"]}
                        before={<Icon28DocumentOutline/>}
                        after={
                            <IconButton style={{color: "var(--accent)"}} onClick={() => {
                                api.delete(doc[Object.keys(doc)[0]]["$oid"]).then()
                                window.location.replace("https://dagvodres.ru/panel")
                            }}>
                                <Icon28CancelCircleFillRed/>
                            </IconButton>
                        }
                    >
                        {Object.keys(doc)[0]}
                    </SimpleCell>
                ])
            })
        })
    }, [])

    return (
        <Panel id={id}>
            <PanelHeader/>
            <Group style={{paddingTop: desktop ? "16px" : "0"}}>
                <Group>
                    <Header
                        mode="primary"
                    >
                        Добавление событий
                    </Header>
                    <FormLayout>
                        <FormLayoutGroup>
                            <FormItem top="Название будет отображаться только в панели.">
                                <Input
                                    type="text"
                                    onChange={(e) => setEventUniqueName(e.target.value)}
                                    placeholder="Введите название события"
                                />
                            </FormItem>
                            <FormItem top="Загрузите ваше фото">
                                <File before={<Icon24Camera role="presentation" />}
                                      size="l"
                                      stretched
                                      appearance="positive"
                                      accept=".png, .jpg, .jpeg"
                                      onChange={e => onFileInputChange(e).then(r => {
                                          setImage(r)
                                      })}
                                >
                                    Загрузить
                                </File>
                            </FormItem>
                        </FormLayoutGroup>
                        <FormLayoutGroup>
                            <FormItem top="Заголовок">
                                <Textarea
                                    onChange={(e) => setHeader(e.target.value)}
                                    rows={1}
                                    placeholder="Введите заголовок события"
                                />
                            </FormItem>
                            <FormItem top="Текст">
                                <Textarea
                                    onChange={(e) => setText(e.target.value)}
                                    rows={1}
                                    placeholder="Введите текст события"
                                />
                            </FormItem>
                            <FormItem top="Максимальная высота">
                                <Input
                                    onChange={(e) => setMaxHeight(e.target.value)}
                                    type="text"
                                    placeholder="Введите максимальную высоту"
                                />
                            </FormItem>
                        </FormLayoutGroup>
                    </FormLayout>

                    <Div>
                        <Separator/>
                    </Div>

                    <div style={{margin: "0 10px 0 10px"}}>
                        <Header
                            mode="secondary"
                        >
                            Предосмотр события
                        </Header>
                        <ContentCard
                            style={{cursor: "default"}}
                            src={image}
                            header={header}
                            text={text}
                            maxHeight={Number(maxHeight) <= 0 ? 300 : Number(maxHeight)}
                        />
                    </div>

                    <Div>
                        <Separator/>
                    </Div>

                    <Div>
                        <div style={{display: "flex", justifyContent: "center"}}>
                            <Button
                                onClick={() => {
                                    api.add_event({
                                        header: header,
                                        text: text,
                                        maxHeight: Number(maxHeight) <= 0 ? 300 : Number(maxHeight),
                                        uniqueName: eventUniqueName,
                                        type: "photo"
                                    }, {
                                        src: image
                                    }).then(() => {
                                        window.location.replace("https://dagvodres.ru/panel")
                                    })
                                }}
                            >
                                Добавить событие
                            </Button>
                        </div>
                    </Div>
                </Group>

                <Group>
                    <Header
                        mode="primary"
                    >
                        Добавление документов
                    </Header>
                    <FormItem top="Это название документа, оно отображается везде.">
                        <Input
                            type="text"
                            onChange={(e) => setDocUniqueName(e.target.value)}
                            placeholder="Введите название документа"
                        />
                    </FormItem>
                    <FormItem top="Загрузите ваш документ">
                        <File before={<Icon24WriteOutline role="presentation" />}
                              size="l"
                              stretched
                              appearance="positive"
                              onChange={e => onFileInputChange(e).then(r => {
                                  setDoc(r)
                              })}
                        >
                            Загрузить
                        </File>
                    </FormItem>

                    <Div>
                        <Separator/>
                    </Div>

                    <div style={{margin: "0 10px 0 10px"}}>
                        <Header
                            mode="secondary"
                        >
                            Предосмотр документа
                        </Header>
                        <SimpleCell
                            key={docs.length}
                            before={<Icon28DocumentOutline/>}
                            after={
                                <IconButton style={{color: "var(--accent)"}}>
                                    <Icon28DownloadCloudOutline/>
                                </IconButton>
                            }
                        >
                            {docUniqueName}
                        </SimpleCell>
                    </div>

                    <Div>
                        <Separator/>
                    </Div>

                    <Div>
                        <div style={{display: "flex", justifyContent: "center"}}>
                            <Button
                                onClick={() => {
                                    api.add_document({
                                        uniqueName: docUniqueName,
                                        type: "doc"
                                    }, {
                                        src: doc
                                    }).then(() => {
                                        window.location.replace("https://dagvodres.ru/panel")
                                    })
                                }}
                            >
                                Добавить документ
                            </Button>
                        </div>
                    </Div>
                </Group>



                <Div>
                    <Div>
                        <Separator/>
                    </Div>
                </Div>

                <Group>
                    <Header
                        mode="secondary"
                        indicator={events.length}
                    >
                        События
                    </Header>
                    <List>
                        {events}
                    </List>
                </Group>

                <Div>
                    <Separator/>
                </Div>

               <Group>
                   <Header
                       mode="secondary"
                       indicator={docs.length}
                   >
                       Документы
                   </Header>
                   <List>
                       {docs}
                   </List>
               </Group>
            </Group>
        </Panel>

    )
}

export default AdminPanel;