import React from "react";
import ReactDOM from "react-dom";
import {
    createBrowserRouter,
    RouterProvider,
    Navigate,
} from "react-router-dom";

import App from "./App";

import { AdaptivityProvider, ConfigProvider, WebviewType } from "@vkontakte/vkui";
import { useAtomValue } from "@mntm/precoil";
import { appearanceAtom } from "./store/atoms";

export const InitializedApp = ( { panel } ) => {
    const appearance = useAtomValue(appearanceAtom)

    return (
        <ConfigProvider platform="ios" webviewType={WebviewType.INTERNAL} appearance={appearance}>
            <AdaptivityProvider hasMouse={true}>
                <App panel={panel}/>
            </AdaptivityProvider>
        </ConfigProvider>
    )
}

const router = createBrowserRouter( [
    {
        path: "/",
        element: <InitializedApp panel={"about"}/>,
    },
    {
        path: "/actions",
        element: <InitializedApp panel={"actions"}/>,
    },
    {
        path: "/documents",
        element: <InitializedApp panel={"documents"}/>,
    },
    {
        path: "/contacts",
        element: <InitializedApp panel={"contacts"}/>,
    },
    {
      path: "/panel",
      element: <InitializedApp panel={"admin"}/>
    },
    {
        path: "*",
        element: <Navigate to="/"/>
    }
]);

ReactDOM.render(
    <RouterProvider router={router} />,
    document.getElementById("root")
);
