import React from "react";

import {
    Group,
    Panel,
    PanelHeader,
    SimpleCell,
    IconButton,
    List
} from "@vkontakte/vkui";

import { Icon28DocumentOutline, Icon28DownloadCloudOutline } from '@vkontakte/icons';

import { useAtomValue } from "@mntm/precoil";
import { desktopAtom } from "../../store/atoms";

import API from "../../store/api/api";
import "../../css/documents.css"


const Documents = ({ id }) => {
    const desktop = useAtomValue(desktopAtom)
    const [docs, setDocs] = React.useState([])
    const api = new API()

    React.useEffect(() => {
        api.get_documents().then(r => {
            r.documents.map((document) =>
                setDocs(docs => [
                    <SimpleCell
                        key={docs.length}
                        before={<Icon28DocumentOutline/>}
                        after={
                            <a download={document.uname} href={document.src} key={docs.length}>
                                <IconButton style={{color: "var(--accent)"}}>
                                    <Icon28DownloadCloudOutline/>
                                </IconButton>
                            </a>
                        }
                    >
                        {document.uname}
                    </SimpleCell>, ...docs
                ])
            )
        })
    }, [])

    return (
        <Panel id={id}>
            <PanelHeader/>
            <Group style={{paddingTop: desktop ? "16px" : "0"}}>
                <List>
                    {docs}
                </List>
            </Group>
        </Panel>

    )
}

export default Documents;