import { API_URL } from "./constants";
import HTTPClient from "./http";


export default class API {
    constructor() {
        this.http = new HTTPClient(API_URL, 100000);
    }

    async request(method, data) {
        const response = await this.http.request(`${method}`, data);
        return response.data;
    }

    async post_request(method, data, payload) {
        const response = await this.http.post(`${method}`, data, payload);
        return response.data;
    }

    async get_events() {
        return await this.request("getEvents");
    }

    async get_event(event_id) {
        return await this.request(`getEvent/${event_id}`)
    }

    async delete(uid) {
        await this.request(`delete/${uid}`)
    }

    async add_event(data, payload) {
        await this.post_request("addEvent", data, payload)
    }

    async add_document(data, payload) {
        await this.post_request("addDocument", data, payload)
    }

    async get_documents() {
        return await this.request("getDocuments");
    }

    async get_event_names() {
        return await this.request(`getEventNames`)
    }

    async get_document_names() {
        return await this.request(`getDocumentNames`)
    }
}