import React from "react";

import {
    Gradient,
    SizeType,
    useAdaptivity,
    Avatar,
    Title,
    Group,
    Panel,
    PanelHeader,
    ContentCard,
    Separator, Div,
    Subhead,
    MiniInfoCell, Footer,
} from '@vkontakte/vkui';

import { Icon24LogoInstagram, Icon24MailOutline, Icon24PhoneOutline } from '@vkontakte/icons'

import logo from "../../assets/logo.png"
import object from "../../assets/object-front_view.png"
import observing_object from "../../assets/observing_object.jpeg"

import "./../../css/about.css"


const About = ({ id }) => {
    const { sizeX } = useAdaptivity()
    const date = new Date()
    const [month, day, year] = [date.getMonth(), date.getDate(), date.getFullYear()]

    const styles = {
        margin: sizeX === SizeType.REGULAR ? "-7px -7px 0 -7px" : 0,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        textAlign: "center",
        padding: 32,
    };

    return (
        <Panel id={id}>
            <PanelHeader/>
            <Group>
                <Gradient style={styles}>

                    <Subhead
                        weight={3}
                        style={{paddingBottom: "16px"}}
                    >
                        ФЕДЕРАЛЬНОЕ АГЕНТСТВО ВОДНЫХ РЕСУРСОВ
                    </Subhead>

                    <Avatar size={96} src={logo}/>

                    <Title
                        style={{ marginBottom: 8, marginTop: 20 }}
                        level={3}
                        weight={2}
                    >
                        МИНИСТЕРСТВО ПРИРОДНЫХ РЕСУРСОВ И ЭКОЛОГИИ <br/>
                        РОССИЙСКОЙ ФЕДЕРАЦИИ <br/>
                        ФГБВУ "ЦЕНТРРЕГИОНВОДХОЗ"
                    </Title>

                </Gradient>

                <Div>
                    <Separator/>
                </Div>

                <ContentCard
                    style={{cursor: "default"}}
                    src={object}
                    subtitle="17.06.2008"
                    header="Административный корпус ФГУ"
                    text="г. Махачкала, пр.Акушинского, 19-я линия, д 23 (367027)"
                    maxHeight={450}
                />

                <Div>
                    <Separator/>
                </Div>

                <MiniInfoCell
                    before={<Icon24MailOutline />}
                    onClick={() => window.location.replace("mailto:contact@dagvodres.ru")}
                >
                    Почта: contact@dagvodres.ru
                </MiniInfoCell>
                <MiniInfoCell
                    before={<Icon24PhoneOutline />}
                    onClick={() => window.location.replace("tel:89882921337")}
                >
                    Телефон: +7 (988) 292-13-37
                </MiniInfoCell>

                <Div>
                    <Separator/>
                </Div>

                <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <iframe
                        id={"ymap"}
                        src="https://yandex.ru/map-widget/v1/?um=constructor%3A758d0b9fc2cfafcd60c6bc025872392824d6fb0d037e93719e457c465d4c7110&amp;source=constructor"
                        width="900" height="400"
                        frameBorder="0"
                    />
                </div>

                <Div>
                    <Separator/>
                </Div>

                <ContentCard
                    style={{cursor: "default"}}
                    src={observing_object}
                    maxHeight={500}
                    subtitle="Место слияния рек `Аварское Койсу` и `Андийское Койсу`"
                />
            </Group>
            <Footer>© {day}.{month <= 9 ? "0" + month : month}.{year} — Филиал Дагводресурсы. </Footer>
        </Panel>

    )
}

export default About;