import React from 'react';

import { PanelHeader, Button, IconButton, Alert } from "@vkontakte/vkui";
import { Icon28MoonOutline, Icon28SunOutline } from "@vkontakte/icons";

import { changeAppearance, changePanel } from "../store/utils";

import { useAtomState, useAtomValue, useSetAtomState } from "@mntm/precoil";
import { desktopAtom, appearanceAtom, activePanelAtom } from "../store/atoms";
import { PopoutContext } from "../store/context";

const openMenu = (setPopout, setPanel) =>
    setPopout(
        <Alert
            actions={[
                {
                    title: "Главная",
                    mode: "default",
                    autoclose: true,
                    action: () => changePanel("about", setPanel)
                },
                {
                    title: "События",
                    mode: "default",
                    autoclose: true,
                    action: () => changePanel("actions", setPanel)
                },
                {
                    title: "Документы",
                    mode: "default",
                    autoclose: true,
                    action: () => changePanel("documents", setPanel)
                },
                {
                    title: "Контакты",
                    mode: "default",
                    autoclose: true,
                    action: () => setPanel("contacts")
                },
                {
                    title: "Закрыть",
                    autoclose: true,
                    mode: "cancel",
                },
            ]}
            actionsLayout="vertical"
            onClose={() => setPopout(null)}
            header="Меню"
            text="Выберите раздел сайта"
        />
    );

const TitleHeader = (props) => {
    const [appearance, setAppearance] = useAtomState(appearanceAtom)
    const setPanel = useSetAtomState(activePanelAtom)
    const appearance_icons = {dark: <Icon28MoonOutline/>, light: <Icon28SunOutline/>}
    const popoutContext = React.useContext(PopoutContext)
    const ButtonRef = React.createRef()

    const desktop = useAtomValue(desktopAtom)

    return (
        <div>
            <PanelHeader
                separator={false}
                style={{zIndex: 100}}
                {...props}
                left={
                    <div style={{alignItems: "center"}}>
                        <IconButton
                            className={desktop ? "header-button header-button-desktop" : "header-button"}
                            onClick={() => changeAppearance(appearance, setAppearance)}
                        >
                            {appearance_icons[appearance === "dark" ? "light" : "dark"]}
                        </IconButton>
                    </div>
                }
                right={
                    <Button
                        className={desktop ? "header-button header-button-desktop login-button-desktop" : "header-button"}
                        getRootRef={ButtonRef}
                        onClick={() => {openMenu(popoutContext.setPopout, setPanel)}}
                    >
                        меню
                    </Button>
                }
            >
                ДагВодРесурсы
            </PanelHeader>
            <div id={"gradient-line"} style={{position: "fixed"}}/>
        </div>
    );
};

export default TitleHeader;