import React from 'react';

import {
    AppRoot,
    SplitLayout,
    SplitCol,
    useAdaptivity,
    ViewWidth,
    View
} from '@vkontakte/vkui';

import About from "./panels/about/About";
import Actions from "./panels/actions/Actions";
import Documents from "./panels/documents/Documents";
import Panel from "./panels/admin/Panel";


import TitleHeader from "./components/TitleHeader";

import { useAtomState, useSetAtomState } from "@mntm/precoil";
import { activePanelAtom, desktopAtom } from "./store/atoms";
import { PopoutContext } from "./store/context";

import '@vkontakte/vkui/dist/vkui.css';
import './css/style.css'


const App = ( { panel } ) => {
    const [activePanel, setActivePanel] = useAtomState(activePanelAtom)
    const [popout, setPopout] = React.useState(null)
    const setDesktop = useSetAtomState(desktopAtom)

    React.useEffect(() => {
        if (window.location.pathname !== "/" || panel === "admin") {
            window.history.replaceState({}, document.title, "/")
            setActivePanel(panel)
        }
    }, [])



    const { viewWidth } = useAdaptivity()
    const desktop = viewWidth >= ViewWidth.SMALL_TABLET; setDesktop(desktop)

    return (
        <AppRoot>
            <div id={"shadow"} style={{zIndex: 1000}}/>
            <PopoutContext.Provider value={{popout, setPopout}}>
                <SplitLayout
                    style={{ justifyContent: "center" }}
                    header={<TitleHeader/>}
                    popout={popout}
                >
                    <SplitCol maxWidth={950}>
                        <View activePanel={activePanel}>
                            <About id="about"/>
                            <Actions id="actions"/>
                            <Documents id="documents"/>
                            <Panel id="admin"/>
                        </View>
                    </SplitCol>
                </SplitLayout>
            </PopoutContext.Provider>
        </AppRoot>
    );
}

export default App;
