export const enableShadow = () => {
    const shadow = document.getElementById("shadow")
    shadow.style.display = "block"
    shadow.classList.add("shadow")
    setTimeout(() => {
        setTimeout(() => {
            shadow.classList.remove("shadow")
            shadow.style.display = "none"
        }, 1000)
    }, 1000)
}

export const changeAppearance = (appearance, setAppearance) => {
    enableShadow()
    setTimeout(() => {
        setAppearance(appearance === "dark" ? setAppearance("light") : setAppearance("dark"))
    }, 1000)
}

export const changePanel = (panel, setPanel) => {
    enableShadow()
    setTimeout(() => {
        setPanel(panel)
    }, 1000)
}